var app = angular.module('datePickerDemo');

app.controller('tagsController', function ($scope) {
  $scope.inputTags = [{name:"foo"},{name:"bar"}];

  $scope.addTag = function () {
    if ($scope.tagText.length == 0) {
      return;
    }

    $scope.inputTags.push({name: $scope.tagText});
    $scope.tagText = '';
  }

  $scope.deleteTag = function (key) {
    if ($scope.inputTags.length > 0 &&
      $scope.tagText.length == 0 &&
      key === undefined) {
      $scope.inputTags.pop();
    } else if (key != undefined) {
      $scope.inputTags.splice(key, 1);
    }
  }
});

app.directive('tagInput', function () {
  return {
    restrict: 'A',
    link: function (scope, element, attrs) {
      scope.inputWidth = 50;

      // Watch for changes in text field
      scope.$watch(attrs.ngModel, function (value) {
        if (value != undefined) {
          var tempEl = $('<span>' + value + '</span>').appendTo('body');
          scope.inputWidth = tempEl.width() + 5;
          tempEl.remove();
        }
      });

      element.bind('keydown', function (e) {
        if (e.which == 9) {
          e.preventDefault();
        }

        if (e.which == 8) {
          scope.$apply(attrs.deleteTag);
        }
      });

      element.bind('keyup', function (e) {
        var key = e.which;

        // Tab or Enter pressed
        if (key == 9 || key == 13) {
          e.preventDefault();
          scope.$apply(attrs.newTag);
        }
      });
    }
  }
});


(function() {
  'use strict';

  angular
    .module('datePickerDemo')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, webDevTec, toastr) {
    var vm = this;

    vm.awesomeThings = [];
    vm.classAnimation = '';
    vm.creationDate = 1452348241685;
    vm.showToastr = showToastr;

    activate();

    function activate() {
      getWebDevTec();
      $timeout(function() {
        vm.classAnimation = 'rubberBand';
      }, 4000);
    }

    function showToastr() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
      vm.classAnimation = '';
    }

    function getWebDevTec() {
      vm.awesomeThings = webDevTec.getTec();

      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    }
  }
})();

